import React from 'react';
import PropTypes from 'prop-types';
import { Box, Typography, Card, CardContent, Tabs, Tab, Toolbar, FormControlLabel, FormControl } from '@mui/material';
import CustomTextField from '../../forms/custom-elements/CustomTextField';
import CustomRadio from '../../forms/custom-elements/CustomRadio';
import CustomFormLabel from '../../forms/custom-elements/CustomFormLabel';


const IdentificationPanel = () => {
    const [selectedValue, setSelectedValue] = React.useState('');

    const handleChange3 = (event) => {
        setSelectedValue(event.target.value);
    };
     return <div className="tabpanelContent">
        <Typography className='panelHeading'>Identification</Typography>
        <CustomTextField
            id="search"
            placeholder="First Name"
            variant="outlined"
            fullWidth
        // size="small"
        // value={search}
        // onChange={(e) => setSearch(e.target.value)}
        />

        <CustomTextField
            id="search"
            placeholder="Middle Name"
            variant="outlined"
            fullWidth
        // size="small"
        // value={search}
        // onChange={(e) => setSearch(e.target.value)}
        />
        <CustomTextField
            id="search"
            placeholder="Last Name"
            variant="outlined"
            fullWidth
        // size="small"
        // value={search}
        // onChange={(e) => setSearch(e.target.value)}
        />
        <CustomTextField
            id="date"
            type="date"
            placeholder="D.O.B"
            variant="outlined"
            fullWidth
            InputLabelProps={{
                shrink: true,
            }}
        // size="small"
        />
        <FormControl
            sx={{
                width: '100%',
            }}
        >
            <CustomFormLabel>Gender</CustomFormLabel>
            <Box>
                <FormControlLabel
                    checked={selectedValue === 'a'}
                    onChange={handleChange3}
                    value="a"
                    label="Female"
                    name="radio-button-demo"
                    control={<CustomRadio />}
                    inputprops={{ 'aria-label': 'A' }}
                />
                <FormControlLabel
                    checked={selectedValue === 'b'}
                    onChange={handleChange3}
                    value="b"
                    label="Male"
                    control={<CustomRadio />}
                    name="radio-button-demo"
                    inputprops={{ 'aria-label': 'B' }}
                />
            </Box>
        </FormControl>
        <FormControl
            sx={{
                width: '100%',
            }}
        >
            <CustomFormLabel>Status</CustomFormLabel>

            <Box>
                <FormControlLabel
                    checked={selectedValue === 'a'}
                    onChange={handleChange3}
                    value="a"
                    label="Active"
                    name="radio-button-demo"
                    control={<CustomRadio />}
                    inputprops={{ 'aria-label': 'A' }}
                />
                <FormControlLabel
                    checked={selectedValue === 'b'}
                    onChange={handleChange3}
                    value="b"
                    label="Inactive"
                    control={<CustomRadio />}
                    name="radio-button-demo"
                    inputprops={{ 'aria-label': 'B' }}
                />
            </Box>
        </FormControl>
    </div>
}
export default IdentificationPanel