import React, { useRef } from 'react';
import PropTypes from 'prop-types';
import { Box, Typography, Card, CardContent, Tabs, Tab, Toolbar, Button } from '@mui/material';
import FeatherIcon from 'feather-icons-react';
import { alpha } from '@mui/material/styles';
import PageContainer from '../container/PageContainer';
import Breadcrumb from '../../layouts/full-layout/breadcrumb/Breadcrumb';
import IdentificationPanel from './TabPanel/IdentificationPanel';
import GuardiansPanel from './TabPanel/GuardiansPanel';
import MedicalInfoPanel from './TabPanel/MedicalInfoPanel';
import CoveragePanel from './TabPanel/CoveragePanel';
import OtherPanel from './TabPanel/OtherPanel';
import AddressPanel from './TabPanel/AddressPanel';

const TabPanel = (props) => {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            aria-labelledby={`simple-tab-${index}`}
            {...other}
        >
            {value === index && <Box sx={{ pt: 3 }}>{children}</Box>}
        </div>
    );
};

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.number.isRequired,
    value: PropTypes.number.isRequired,
};

const a11yProps = (index) => {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`,
    };
};
const BCrumb = [
    {
        to: '>',
        title: 'Patients',
    },
    {
        title: 'Add new patient',
    },
];
const NewPtient = () => {
    const [value, setValue] = React.useState(0);
    const identificationRef = useRef(null)
    const addressRef = useRef(null)
    const guardianRef = useRef(null)
    const medicalInfoRef = useRef(null)
    const coverageRef = useRef(null)
    const otherRef = useRef(null)

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    const scrollDown = (ref) => {
        console.log(ref.current.offsetTop, ref.current.offsetTop+200);
        let scrollToPx = ref.current.offsetTop-100
        window.scrollTo({
            top: scrollToPx ,
            behavior: 'smooth',
        });
    };

    return (
        <PageContainer title="Patient" description="this is new Patient page">
            <Breadcrumb title="" items={BCrumb} />
            <Card
                sx={{
                    p: {
                        xs: '0px',
                        sm: '0px',
                        lg: '0px',
                    },
                }}
            >
                <CardContent
                    sx={{
                        p: 0,
                    }}

                >
                    <Toolbar
                        sx={{
                            pl: { sm: 2 },
                            pr: { xs: 1, sm: 1 },
                            // ...(numSelected > 0 && {
                             bgcolor: (theme) =>
                                alpha(theme.palette.primary.main, theme.palette.action.activatedOpacity),
                            // }),
                        }}
                    >

                        <Typography sx={{ flex: '1 1 100%' }} variant="h6" id="patientTableTitle" component="div">
                            Add a new patient
                        </Typography>
                    </Toolbar>
                    <Box sx={{
                        width: '100%',
                        p: {
                            xs: '20px',
                            sm: '35px',
                            lg: '35px',
                        },
                        pt: {
                            xs: '10px',
                            sm: '10px',
                            lg: '10px',
                        }
                    }}
                    className="newPatientCardBox"
                    >
                        <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
                            <Tabs
                                value={value}
                                onChange={handleChange}
                                aria-label="basic tabs example"
                                textColor="secondary"
                                allowScrollButtonsMobile
                                scrollButtons
                                indicatorColor="secondary"
                            >
                                <Tab
                                    sx={{
                                        textTransform: 'capitalize',
                                    }}
                                    label="Identification"
                                    {...a11yProps(0)}
                                    onClick={() => scrollDown(identificationRef)}
                                />
                                <Tab
                                    sx={{
                                        textTransform: 'capitalize',
                                    }}
                                    label="Address"
                                    {...a11yProps(1)}
                                    onClick={() => scrollDown(addressRef)}
                                    />
                                <Tab
                                    sx={{
                                        textTransform: 'capitalize',
                                    }}
                                    label="Guardians"
                                    {...a11yProps(2)}
                                    onClick={() => scrollDown(guardianRef)}
                                    />
                                <Tab
                                    sx={{
                                        textTransform: 'capitalize',
                                    }}
                                    label="Medical Info"
                                    {...a11yProps(3)}
                                    onClick={() => scrollDown(medicalInfoRef)}
                                    />
                                <Tab
                                    sx={{
                                        textTransform: 'capitalize',
                                    }}
                                    label="Coverage Details"
                                    {...a11yProps(4)}
                                    onClick={() => scrollDown(coverageRef)}
                                    />
                                <Tab
                                    sx={{
                                        textTransform: 'capitalize',
                                    }}
                                    label="Other"
                                    {...a11yProps(5)}
                                    onClick={() => scrollDown(otherRef)}
                                    />
                            </Tabs>
                        </Box>
                        <div ref={identificationRef}>
                            <IdentificationPanel />
                        </div>
                        <div ref={addressRef}>
                            <AddressPanel />
                        </div>
                        <div ref={guardianRef}>
                            <GuardiansPanel />
                        </div>
                        <div ref={medicalInfoRef}>
                            <MedicalInfoPanel />
                        </div>
                        <div ref={coverageRef}>
                            <CoveragePanel />
                        </div>
                        <div ref={otherRef}>
                            <OtherPanel />
                        </div>

                        {/* <TabPanel value={value} index={0} component="div">
                            <IdentificationPanel useRef />
                            <AddressPanel />
                        </TabPanel>
                        <TabPanel value={value} index={1}>
                            <AddressPanel />
                        </TabPanel>
                        <TabPanel value={value} index={2}>
                            <GuardiansPanel />
                        </TabPanel>
                        <TabPanel value={value} index={3} component="div">
                            <MedicalInfoPanel />
                        </TabPanel>
                        <TabPanel value={value} index={4}>
                            <CoveragePanel />
                        </TabPanel>
                        <TabPanel value={value} index={5}>
                            <OtherPanel />
                        </TabPanel> */}
                        <Button variant="contained" color="primary" className='registerButton'>
                            <FeatherIcon icon="plus" style={{ width: 15, height: 15 }} />
                            Register Patient
                        </Button>
                    </Box>

                </CardContent>
            </Card>
        </PageContainer>
    );

};

export default NewPtient;
